<template>
  <div class="super-flow-base-demo">
    <template>
    <super-flow
      ref="superFlow"
      :node-list="nodeList"
      :link-list="linkList"
      :origin="origin"
      :graph-menu="graphMenuList"
      :node-menu="nodeMenuList"
      :link-menu="linkMenuList"
      :enter-intercept="enterIntercept"
      :output-intercept="outputIntercept"
      :link-desc="linkDesc">
      <template v-slot:node="{meta}">

        <div class="startNode" v-if="meta.prop === 'start'">
          <div class="inner">
            <h6>开始</h6>
          </div>
        </div>

        <div class="endNode" v-else-if="meta.prop === 'end'">
          <div class="inner">
            <h6>结束</h6>
          </div>
        </div>

        <div 
          v-else 
          :class="`flow-node otherNode`"
          @click="SingleClick(meta)"  
          @dblclick="DoubleClick(meta)">

          <el-tooltip 
            class="item" 
            effect="dark"
            v-if="meta.desc.length>5"
            :content="meta.desc" 
            placement="right">
            <section v-if="meta.selectStatus" class="Active">
              {{meta.desc}}
            </section>
            <section v-if="!meta.selectStatus">
              {{meta.desc}}
            </section>
          </el-tooltip>
 
          <template v-if="meta.desc.length<=5">
              <section 
                v-if="meta.selectStatus" 
                class="Active">
                {{meta.desc}}
              </section>
              <section 
                v-if="!meta.selectStatus">
                {{meta.desc}}
              </section>
          </template>
          
        </div>
      </template>
    </super-flow>
    </template>

    <el-dialog
      :title="drawerConf.title"
      :visible.sync="drawerConf.visible"
      :close-on-click-modal="false"
      width="500px">
      <el-form
        @keyup.native.enter="settingSubmit"
        @submit.native.prevent
        v-show="drawerConf.type === drawerType.node"
        ref="nodeSetting"
        :model="nodeSetting">
        <el-form-item
          label="节点描述"
          prop="desc">
          <el-input
            v-model="nodeSetting.desc"
            placeholder="请输入节点描述">
          </el-input>
        </el-form-item>
      </el-form>
      <el-form
        @keyup.native.enter="settingSubmit"
        @submit.native.prevent
        v-show="drawerConf.type === drawerType.link"
        ref="linkSetting"
        :model="linkSetting">
        <el-form-item
          label="连线描述"
          prop="desc">
          <el-input
            v-model="linkSetting.desc"
            placeholder="请输入连线描述">
          </el-input>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer">
        <el-button
          @click="drawerConf.cancel">
          取 消
        </el-button>
        <el-button
          type="primary"
          @click="settingSubmit">
          确 定
        </el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="节点"
      :visible.sync="newVisible"
      :close-on-click-modal="false"
      width="500px">
      <el-form
        @keyup.native.enter="conformSubmit"
        @submit.native.prevent
        ref="newNodeSetting">
        <el-form-item
          label="节点描述"
          prop="desc">
          <el-input
            v-model="EditorDesc"
            placeholder="请输入节点描述">
          </el-input>
        </el-form-item>
      </el-form>
      <span
      slot="footer"
      class="dialog-footer">
        <el-button
          @click="cancelDrawerConf">
          取 消
        </el-button>
        <el-button
          type="primary"
          @click="conformSubmit">
          确 定
        </el-button>
      </span>
    </el-dialog>


  </div>
</template>

<script>
  const drawerType = {
    node: 0,
    link: 1
  }
  import tsconfig from '../config/newConfig.json'
  import data from '../config/interface.json'
  import { 
    fetchData, 
    sentData 
  } from '../api/article'
  export default {
    data() {
      return {
        selectNode:{},
        EditorDesc:'',
        newVisible:false,
        graph: {},
        drawerType,
        drawerConf: {
          title: '',
          visible: false,
          type: null,
          info: null,
          open: (type, info) => {
            const conf = this.drawerConf
            conf.visible = true
            conf.type = type
            conf.info = info
            if (conf.type === drawerType.node) {
              conf.title = '节点'
              if (this.$refs.nodeSetting) this.$refs.nodeSetting.resetFields()
              this.$set(this.nodeSetting, 'name', info.meta.name)
              this.$set(this.nodeSetting, 'desc', info.meta.desc)
            } else {
              conf.title = '连线'
              if (this.$refs.linkSetting) this.$refs.linkSetting.resetFields()
              this.$set(this.linkSetting, 'desc', info.meta ? info.meta.desc : '')
            }
          },
          cancel: () => {
            this.drawerConf.visible = false
            if (this.drawerConf.type === drawerType.node) {
              this.$refs.nodeSetting.clearValidate()
            } else {
              this.$refs.linkSetting.clearValidate()
            }
          }
        },
        linkSetting: {
          desc: ''
        },
        nodeSetting: {
          name: '',
          desc: ''
        },

        origin: [300, 465],
        nodeList: [],
        linkList: [],

        graphMenuList: [
          [
            {
              label: '开始节点',
              disable(graph) {
                return !!graph.nodeList.find(node => node.meta.prop === 'start')
              },
              selected: (graph, coordinate) => {
                const start = graph.nodeList.find(node => node.meta.prop === 'start')
                if (!start) {
                  graph.addNode({
                    width: 100,
                    height: 80,
                    coordinate: coordinate,
                    meta: {
                      prop: 'start',
                      name: '开始节点'
                    }
                  })
                }
              }
            },
            {
              label: '条件节点',
              disable: false,
              selected: (graph, coordinate) => {
                let params = {
                  circularFlag: false,
                  coordinate: coordinate,
                  height: '55.0',
                  id: `Rid${Math.round(Math.random() * 1000000)}`,
                  meta: {
                    desc: '',
                    name: `Rid${Math.round(Math.random() * 1000000)}`,
                    prop: 'cc',
                    editStatus: false,
                    selectStatus: false
                  },
                  name: `Rid${Math.round(Math.random() * 10000000)}`,
                  prop: null,
                  status: '0',
                  width: '105.0'
                }
                graph.addNode(params);
                console.log('graph_____',graph);
                //this.nodeList = graph.nodeList;
              }
            },
            {
              label: '结束节点',
              disable(graph) {
                return !!graph.nodeList.find(point => point.meta.prop === 'end')
              },
              selected: (graph, coordinate) => {
                graph.addNode({
                  width: 80,
                  height: 50,
                  coordinate: coordinate,
                  meta: {
                    prop: 'end',
                    name: '结束节点'
                  }
                })
              }
            }
          ],
          [
            {
              label: '发送数据',
              selected: (graph) => {
                let totalData = graph.toJSON();
                let {nodeList, linkList }= totalData
                this.formatNode(nodeList,linkList);
                this.formatLink(linkList);
                this.getPoints();
                              
                console.log(nodeList);
                console.log(linkList);
                console.log(tsconfig); 
                console.log(this.graph);
              }
            },
            {
              label: '全选',
              selected: (graph, coordinate) => {
                console.log(coordinate)
                graph.selectAll()
              }
            }
          ]
        ],
        nodeMenuList: [
          [
            {
              label: '删除',
              disable: false,
              hidden(node) {
                return node.meta.prop === 'start'
              },
              selected(node, coordinate) {
                console.log(coordinate)
                node.remove()
              }
            }
          ],
          [
            {
              label: '编辑',
              selected: (node, coordinate) => {
                console.log(coordinate)
                console.log('drawerType.node___',drawerType.node);
                console.log('node___',node);
                this.drawerConf.open(drawerType.node, node)
              }
            }
          ]
        ],
        linkMenuList: [
          [
            {
              label: '删除',
              disable: false,
              selected: (link, coordinate) => {
                console.log(coordinate)
                link.remove()
              }
            }
          ],
          [
            {
              label: '编辑',
              disable: false,
              selected: (link, coordinate) => {
                console.log(coordinate)
                this.drawerConf.open(drawerType.link, link)
              }
            }
          ]
        ]
      }
    },
    created() {},
    mounted(){
      this.setOriginData();
      //获取数据
      this.getData();
    },
    methods: {
      async getData(){
        let myJson = await fetchData();
        this.nodeList = myJson.nodeList;
        this.linkList = myJson.linkList; 
        //节点数据处理
        this.nodeList.map((item)=>{
          item.coordinate[0] = parseInt(item.coordinate[0]) - 400;
          item.coordinate[1] = parseInt(item.coordinate[1]) - 400;
        });
        this.nodeList.map((item)=>{
          item.meta.id = item.id;
        });
        //线条数据处理
        this.linkList.map((item)=>{
          item.startAt = [52.5, 55];
          item.endAt = [52.5, 0];
        });
        //设置节点编辑状态
        this.editStatus();
        //设置节点选中状态
        this.selectStatus();
      },
      setOriginData(){
        //默认数据
        this.nodeList = data.demo2;
        this.linkList = data.demo1;
        this.nodeList.map((item)=>{
          item.coordinate[0] = parseInt(item.coordinate[0]) - 600;
          item.coordinate[1] = parseInt(item.coordinate[1]) - 400;
        });
        this.linkList.map((item)=>{
          item.startAt = [52.5, 55];
          item.endAt = [52.5, 0];
        });
      },
      conformSubmit(){
        this.selectNode.meta.desc  = this.EditorDesc;
        //关闭弹框
        this.newVisible = false;
      },
      cancelDrawerConf(){
        //关闭弹框
        this.newVisible = false;
      },
      DoubleClick(selectItem){
        this.nodeList.map((item)=>{
          if(item.id === selectItem.id){
            console.log('双击事件___',selectItem);
            //解除单击事件状态
            item.meta.selectStatus = false;
            //设置编辑状态
            item.meta.editStatus = true;
            //打开弹框
            this.newVisible = true;
            this.EditorDesc = selectItem.desc;
            //标记选中节点
            this.selectNode = item;
          }
        });
      },
      SingleClick(selectItem){
        this.nodeList.map((item)=>{
          if(item.id === selectItem.id){
            item.meta.selectStatus = !item.meta.selectStatus;
            console.log('单击事件___',item);
            //标记选中节点
            this.selectNode = item;
          }
        })
      },
      editStatus(){
        this.nodeList.map((item)=>{
          item.meta.editStatus = false;
        });
      },
      selectStatus(){
        this.nodeList.map((item)=>{
          item.meta.selectStatus = false;
        });
      },
      enterIntercept(formNode, toNode, graph) {
        console.log(graph)
        const formType = formNode.meta.prop
        switch (toNode.meta.prop) {
          case 'start':
            return false
          case 'approval':
            return [
              'start',
              'approval',
              'condition',
              'cc'
            ].includes(formType)
          case 'condition':
            return [
              'start',
              'approval',
              'condition',
              'cc'
            ].includes(formType)
          case 'end':
            return [
              'start',
              'approval',
              'condition',
              'cc'
            ].includes(formType)
          default:
            return true
        }
      },
      outputIntercept(node, graph) {
        console.log(graph)
        console.log(node)
        this.graph = graph;
        return !(node.meta.prop === 'end')
      },
      linkDesc(link) {
        return link.meta ? link.meta.desc : ''
      },
      settingSubmit() {
        const conf = this.drawerConf
        if (this.drawerConf.type === drawerType.node) {
          if (!conf.info.meta) conf.info.meta = {}
          Object.keys(this.nodeSetting).forEach(key => {
            this.$set(conf.info.meta, key, this.nodeSetting[key])
          })
          this.$refs.nodeSetting.resetFields()
        } else {
          if (!conf.info.meta) conf.info.meta = {}
          Object.keys(this.linkSetting).forEach(key => {
            this.$set(conf.info.meta, key, this.linkSetting[key])
          })
          this.$refs.linkSetting.resetFields()
        }
        conf.visible = false

        //格式化数据，回传服务器
      },
      formatNode(nodeList){
        tsconfig.flowInfo.nodeList = nodeList;
      },
      formatLink(linkList){
        tsconfig.flowInfo.linkList = linkList;
      },
      getPoints(){
        let pointArray = [];
        this.graph.linkList.map((item)=>{
          pointArray.push({
            id: item.id,
            pointList:item.pathPointList.pointList
          })
        });
        tsconfig.flowInfo.linkList.map((item)=>{
          pointArray.map((inner)=>{
            if(inner.id === item.id){
              item.pointList = inner.pointList;
            }
          })
        });
        tsconfig.flowInfo.linkList.map((item)=>{
          item.pointList.map((inner,index)=>{
            item.pointList[index] = {
              x:inner[0],
              y:inner[1]
            }
          })
        });
        this.postData();
      },
      async postData(){
        let res = await sentData(tsconfig);
        console.log(res);
      },
    } 
  }
</script>

