          border:1px solid #d9d9d9;
          background:#000000;
          display:block;
        }
      }
      .flow-node:hover{
        border: 0px solid #2e6e75;
        background-color: #2e6e75;
        height: 52px;
        border-radius:10px;
        color:#ffffff;
      }
    }
  }
</style>