<template>
  <div>
    <div class="super-flow-demo1">

      <div class="node-container">
        <span
          class="node-item"
          v-for="(item,index) in nodeItemList"
          :key="index"
          @mousedown="evt => nodeItemMouseDown(evt, item.value)">
          {{item.label}}
        </span>
      </div>

      <div
        class="flow-container"
        ref="flowContainer">
        <super-flow
          ref="superFlow"
          :node-list="nodeList"
          :link-list="linkList"
          :origin="origin"
          :graph-menu="graphMenu"
          :node-menu="nodeMenu"
          :link-menu="linkMenu"
          :link-base-style="linkBaseStyle"
          :link-style="linkStyle"
          :link-desc="linkDesc"
          :enter-intercept="enterIntercept"
          :output-intercept="outputIntercept">
          <template v-slot:node="{meta}">

            <div class="startNode" v-if="meta.prop === 'start'">
              <div class="inner">
                <h6>开始</h6>
              </div>
            </div>

            <div class="endNode" v-else-if="meta.prop === 'end'">
              <div class="inner">
                <h6>结束</h6>
              </div>
            </div>

            <div
              v-else
              class="flow-node"
              @dblclick="DoubleClick(meta)">

              <el-tooltip 
                class="item" 
                effect="dark"
                v-if="meta.desc.length>5"
                :content="meta.desc" 
                placement="right">
                <section>
                  {{meta.desc}}
                </section>
              </el-tooltip>

              <template v-if="meta.desc.length<=5">
                  <section>
                    {{meta.desc}}
                  </section>
              </template>


            </div>

          </template>
        </super-flow>
      </div>
    </div>

    <el-dialog
      :title="drawerConf.title"
      :visible.sync="drawerConf.visible"
      :close-on-click-modal="false"
      width="500px">
      <el-form
        v-show="drawerConf.type === drawerType.node"
        ref="nodeSetting"
        :model="nodeSetting">
        <el-form-item
          label="节点描述"
          prop="desc">
          <el-input
            v-model="nodeSetting.desc"
            placeholder="请输入节点描述">
          </el-input>
        </el-form-item>
      </el-form>
      <el-form
        v-show="drawerConf.type === drawerType.link"
        ref="linkSetting"
        :model="linkSetting">
        <el-form-item
          label="连线描述"
          prop="desc">
          <el-input
            v-model="linkSetting.desc"
            placeholder="请输入连线描述">
          </el-input>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer">
        <el-button
          @click="drawerConf.cancel">
          取 消
        </el-button>
        <el-button
          type="primary"
          @click="settingSubmit">
          确 定
        </el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="节点"
      :visible.sync="newVisible"
      :close-on-click-modal="false"
      width="500px">
      <el-form
        @keyup.native.enter="conformSubmit"
        @submit.native.prevent
        ref="newNodeSetting">
        <el-form-item
          label="节点描述"
          prop="desc">
          <el-input
            v-model="EditorDesc"
            placeholder="请输入节点描述">
          </el-input>
        </el-form-item>
      </el-form>
      <span
      slot="footer"
      class="dialog-footer">
        <el-button
          @click="cancelDrawerConf">
          取 消
        </el-button>
        <el-button
          type="primary"
          @click="conformSubmit">
          确 定
        </el-button>
      </span>
    </el-dialog>


  </div>
</template>

<script>

  const drawerType = {
    node: 0,
    link: 1
  }
  import tsconfig from '../config/newConfig.json'
  import data from '../config/interface.json'
  import { 
    fetchData, 
    sentData 
  } from '../api/article'
  export default {
    data() {
      return {
        selectNode: {},
        EditorDesc:'',
        newVisible:false,
        drawerType,
        drawerConf: {
          title: '',
          visible: false,
          type: null,
          info: null,
          open: (type, info) => {
            const conf = this.drawerConf
            conf.visible = true
            conf.type = type
            conf.info = info
            if (conf.type === drawerType.node) {
              conf.title = '节点'
              if (this.$refs.nodeSetting) this.$refs.nodeSetting.resetFields()
              this.$set(this.nodeSetting, 'name', info.meta.name)
              this.$set(this.nodeSetting, 'desc', info.meta.desc)
            } else {
              conf.title = '连线'
              if (this.$refs.linkSetting) this.$refs.linkSetting.resetFields()
              this.$set(this.linkSetting, 'desc', info.meta ? info.meta.desc : '')
            }
          },
          cancel: () => {
            this.drawerConf.visible = false
            if (this.drawerConf.type === drawerType.node) {
              this.$refs.nodeSetting.clearValidate()
            } else {
              this.$refs.linkSetting.clearValidate()
            }
          }
        },
        linkSetting: {
          desc: ''
        },
        nodeSetting: {
          desc: ''
        },

        dragConf: {
          isDown: false,
          isMove: false,
          offsetTop: 0,
          offsetLeft: 0,
          clientX: 0,
          clientY: 0,
          ele: null,
          info: null
        },
        nodeItemList: [
          {
            label: '开始节点',
            value: {
              circularFlag: false,
              width: 50,
              height: 50,
              id: `Rid${Math.round(Math.random() * 1000000)}`,
              name: '结束节点',
              prop: null,
              status: '0',
              meta: {
                prop: 'start',
                label: '开始',
                name: '开始'
              }
            }
          },
          {
            label: '条件节点',
            value: {
              circularFlag: false,
              height: '55.0',
              id: `Rid${Math.round(Math.random() * 1000000)}`,
              meta: {
                desc: '',
                name: `Rid${Math.round(Math.random() * 1000000)}`,
                prop: 'cc',
                editStatus: false,
                selectStatus: false
              },
              name: `Rid${Math.round(Math.random() * 10000000)}`,
              prop: null,
              status: '0',
              width: '105.0'
            }
          },
          {
            label: '结束节点',
            value: {
              circularFlag: false,
              width: 50,
              height: 50,
              id: `Rid${Math.round(Math.random() * 1000000)}`,
              name: '结束节点',
              prop: null,
              status: '0',
              meta: {
                prop: 'end',
                label: '结束',
                name: '结束'
              }
            }
          }
        ],

        graphMenu: [
          [
            {
              label: '条件节点',
              disable: false,
              selected: (graph, coordinate) => {
                let params = {
                  circularFlag: false,
                  coordinate: coordinate,
                  height: '55.0',
                  id: `Rid${Math.round(Math.random() * 1000000)}`,
                  meta: {
                    desc: '',
                    name: `Rid${Math.round(Math.random() * 1000000)}`,
                    prop: 'cc',
                    editStatus: false,
                    selectStatus: false
                  },
                  name: `Rid${Math.round(Math.random() * 10000000)}`,
                  prop: null,
                  status: '0',
                  width: '105.0'
                }
                graph.addNode(params);
              }
            }
          ],
          [ 
            {
              label: '发送数据',
              selected: (graph) => {
                let totalData = graph.toJSON();
                let {nodeList, linkList }= totalData
                this.formatNode(nodeList,linkList);
                this.formatLink(linkList);
                this.getPoints();
                              
                console.log(nodeList);
                console.log(linkList);
                console.log(tsconfig); 
                console.log(this.graph);
              }
            },
            {
              label: '全选',
              selected: graph => {
                graph.selectAll()
              }
            }
          ]
        ],
        nodeMenu: [
          [
            {
              label: '删除',
              selected: node => {
                node.remove()
              }
            },
            {
              label: '编辑',
              selected: (node, coordinate )=> {
                console.log(coordinate);
                this.drawerConf.open(drawerType.node, node)
              }
            }
          ]
        ],
        linkMenu: [
          [
            {
              label: '删除',
              selected: link => {
                link.remove()
              }
            },
            {
              label: '编辑',
              selected: link => {
                this.drawerConf.open(drawerType.link, link)
              }
            }
          ]
        ],

        linkBaseStyle: {
          color: '#666666',           // line 颜色
          hover: '#FF0000',           // line hover 的颜色
          textColor: '#666666',       // line 描述文字颜色
          textHover: '#FF0000',       // line 描述文字 hover 颜色
          font: '14px Arial',         // line 描述文字 字体设置 参考 canvas font
          dotted: false,              // 是否是虚线
          lineDash: [4, 4],           // 虚线时生效
          background: 'rgba(255,255,255,0.6)'    // 描述文字背景色
        },
        fontList: [
          '14px Arial',
          'italic small-caps bold 12px arial'
        ],
        nodeList: [],
        linkList: [],
        origin: [300, 465]
      }
    },
    mounted() {
      document.addEventListener('mousemove', this.docMousemove)
      document.addEventListener('mouseup', this.docMouseup)
      this.$once('hook:beforeDestroy', () => {
        document.removeEventListener('mousemove', this.docMousemove)
        document.removeEventListener('mouseup', this.docMouseup)
      })
      //设置默认数据
      this.setOriginData();
      //获取数据
      this.getData();
    },
    methods: {
      DoubleClick(selectItem){
        console.log('this.graph___', this.graph);
        console.log('this.graph___', selectItem);
        this.graph.nodeList.map((item)=>{
          if(item.meta.name === selectItem.name){
            console.log('双击事件___',selectItem);
            //解除单击事件状态
            item.meta.selectStatus = false;
            //设置编辑状态
            item.meta.editStatus = true;
            //打开弹框
            this.newVisible = true;
            this.EditorDesc = selectItem.desc;
            //标记选中节点
            this.selectNode = item;
          }
        });
      },
      enterIntercept(formNode, toNode, graph) {
        console.log(graph)
        switch (toNode.meta.prop) {
          default:
            return true
        }
      },
      outputIntercept(node, graph) {
        console.log(graph)
        console.log(node)
        this.graph = graph;
        return !(node.meta.prop === 'end')
      },
      async getData(){
        let myJson = await fetchData();
        this.nodeList = myJson.nodeList;
        this.linkList = myJson.linkList; 
        //节点数据处理
        this.nodeList.map((item)=>{
          item.coordinate[0] = parseInt(item.coordinate[0]) - 400;
          item.coordinate[1] = parseInt(item.coordinate[1]) - 400;
        });
        this.nodeList.map((item)=>{
          item.meta.id = item.id;
        });
        //线条数据处理
        this.linkList.map((item)=>{
          item.startAt = [52.5, 55];
          item.endAt = [52.5, 0];
        });
        //设置节点编辑状态
        this.editStatus();
        //设置节点选中状态
        this.selectStatus();
      },
      editStatus(){
        this.nodeList.map((item)=>{
          item.meta.editStatus = false;
        });
      },
      selectStatus(){
        this.nodeList.map((item)=>{
          item.meta.selectStatus = false;
        });
      },
      setOriginData(){
        //默认数据
        this.nodeList = data.demo2;
        this.linkList = data.demo1;
        this.nodeList.map((item)=>{
          item.coordinate[0] = parseInt(item.coordinate[0]) - 600;
          item.coordinate[1] = parseInt(item.coordinate[1]) - 400;
        });
        this.linkList.map((item)=>{
          item.startAt = [52.5, 55];
          item.endAt = [52.5, 0];
        });
      },
      formatNode(nodeList){
        tsconfig.flowInfo.nodeList = nodeList;
      },
      formatLink(linkList){
        tsconfig.flowInfo.linkList = linkList;
      },
      getPoints(){
        let pointArray = [];
        this.graph.linkList.map((item)=>{
          pointArray.push({
            id: item.id,
            pointList:item.pathPointList.pointList
          })
        });
        tsconfig.flowInfo.linkList.map((item)=>{
          pointArray.map((inner)=>{
            if(inner.id === item.id){
              item.pointList = inner.pointList;
            }
          })
        });
        tsconfig.flowInfo.linkList.map((item)=>{
          item.pointList.map((inner,index)=>{
            item.pointList[index] = {
              x:inner[0],
              y:inner[1]
            }
          })
        });
        this.postData();
      },
      async postData(){
        let res = await sentData(tsconfig);
        console.log(res);
      },
      flowNodeClick(meta) {
        console.log(meta)
        console.log(this.$refs.superFlow.graph)
      },
      linkStyle(link) {
        console.log(link);
        return {
          // hover: '#FF00FF'
        }
      },
      linkDesc(link) {
        return link.meta ? link.meta.desc : ''
      },
      conformSubmit(){
        this.selectNode.meta.desc  = this.EditorDesc;
        console.log('this.selectNode__', this.selectNode);
        console.log('nodeList__', this.nodeList);
        //关闭弹框
        this.newVisible = false;
      },
      cancelDrawerConf(){
        //关闭弹框
        this.newVisible = false;
      },
      settingSubmit() {
        const conf = this.drawerConf
        if (this.drawerConf.type === drawerType.node) {
          if (!conf.info.meta) conf.info.meta = {}
          Object.keys(this.nodeSetting).forEach(key => {
            this.$set(conf.info.meta, key, this.nodeSetting[key])
          })
          this.$refs.nodeSetting.resetFields()
        } else {
          if (!conf.info.meta) conf.info.meta = {}
          Object.keys(this.linkSetting).forEach(key => {
            this.$set(conf.info.meta, key, this.linkSetting[key])
          })
          this.$refs.linkSetting.resetFields()
        }
        conf.visible = false
      },
      docMousemove({clientX, clientY}) {
        const conf = this.dragConf

        if (conf.isMove) {

          conf.ele.style.top = clientY - conf.offsetTop + 'px'
          conf.ele.style.left = clientX - conf.offsetLeft + 'px'

        } else if (conf.isDown) {

          // 鼠标移动量大于 5 时 移动状态生效
          conf.isMove =
            Math.abs(clientX - conf.clientX) > 5
            || Math.abs(clientY - conf.clientY) > 5

        }
      },
      docMouseup({clientX, clientY}) {
        const conf = this.dragConf
        conf.isDown = false

        if (conf.isMove) {
          const {
            top,
            right,
            bottom,
            left
          } = this.$refs.flowContainer.getBoundingClientRect()

          // 判断鼠标是否进入 flow container
          if (
            clientX > left
            && clientX < right
            && clientY > top
            && clientY < bottom
          ) {

            // 获取拖动元素左上角相对 super flow 区域原点坐标
            const coordinate = this.$refs.superFlow.getMouseCoordinate(
              clientX - conf.offsetLeft,
              clientY - conf.offsetTop
            )

            // 添加节点
            this.$refs.superFlow.addNode({
              coordinate,
              ...conf.info
            })

          }

          conf.isMove = false
        }

        if (conf.ele) {
          conf.ele.remove()
          conf.ele = null
        }
      },
      nodeItemMouseDown(evt, info) {
        const {
          clientX,
          clientY,
          currentTarget
        } = evt

        const {
          top,
          left
        } = evt.currentTarget.getBoundingClientRect()

        const conf = this.dragConf
        const ele = currentTarget.cloneNode(true);
        let param  =  {
          circularFlag: false,
          height: '55.0',
          id: `Rid${Math.round(Math.random() * 1000000)}`,
            meta: {
              desc: '',
              name: `Rid${Math.round(Math.random() * 1000000)}`,
              prop: 'cc',
              editStatus: false,
              selectStatus: false
            },
          name: `Rid${Math.round(Math.random() * 10000000)}`,
          prop: null,
          status: '0',
          width: '105.0'
        }
        info = param;
        Object.assign(this.dragConf, {
          offsetLeft: clientX - left,
          offsetTop: clientY - top,
          clientX: clientX,
          clientY: clientY,
          info,
          ele,
          isDown: true
        })

        ele.style.position = 'fixed'
        ele.style.margin = '0'
        ele.style.top = clientY - conf.offsetTop + 'px'
        ele.style.left = clientX - conf.offsetLeft + 'px'

        this.$el.appendChild(this.dragConf.ele);
      }
    }
  }
</script>

